import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Line } from 'react-chartjs-2';
import 'chart.js/auto';
import {
    Container,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    Typography,
    Box,
    Toolbar,
    AppBar
} from '@mui/material';

const App = () => {
    const [data, setData] = useState({ customers: [], transactions: [] });
    const [filter, setFilter] = useState({ name: '', amount: '' });
    const [selectedCustomer, setSelectedCustomer] = useState(null);

    useEffect(() => {
        axios.get('http://localhost:5000/api/data')
            .then(response => setData(response.data))
            .catch(error => console.error('Error fetching data:', error));
    }, []);

    const handleFilterChange = (e) => {
        const { name, value } = e.target;
        setFilter(prev => ({ ...prev, [name]: value }));
    };

    const handleCustomerSelect = (customer) => {
        setSelectedCustomer(customer);
    };

    const filteredCustomers = data.customers.filter(customer =>
        customer.name.toLowerCase().includes(filter.name.toLowerCase())
    );

    const filteredTransactions = data.transactions.filter(transaction =>
        (!filter.amount || transaction.amount.toString().includes(filter.amount))
    );

    const customerTransactions = selectedCustomer
        ? data.transactions.filter(transaction => transaction.customer_id === selectedCustomer.id)
        : [];

    const chartData = {
        labels: [...new Set(customerTransactions.map(tx => tx.date))],
        datasets: [{
            label: 'Total Transaction Amount',
            data: customerTransactions.reduce((acc, tx) => {
                acc[tx.date] = (acc[tx.date] || 0) + tx.amount;
                return acc;
            }, {}),
            borderColor: 'rgba(75, 192, 192, 1)',
            backgroundColor: 'rgba(75, 192, 192, 0.2)',
        }]
    };

    return (
        <Container>
            <AppBar position="static">
                <Toolbar>
                    <Typography variant="h6">
                        Customer Transactions
                    </Typography>
                </Toolbar>
            </AppBar>
            <Box my={3}>
                <TextField
                    label="Filter by customer name"
                    variant="outlined"
                    name="name"
                    value={filter.name}
                    onChange={handleFilterChange}
                    fullWidth
                    margin="normal"
                />
                <TextField
                    label="Filter by transaction amount"
                    variant="outlined"
                    name="amount"
                    value={filter.amount}
                    onChange={handleFilterChange}
                    fullWidth
                    margin="normal"
                />
            </Box>
            <TableContainer component={Paper}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Customer Name</TableCell>
                            <TableCell>Transaction ID</TableCell>
                            <TableCell>Date</TableCell>
                            <TableCell>Amount</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {filteredCustomers.map(customer =>
                            filteredTransactions
                                .filter(transaction => transaction.customer_id === customer.id)
                                .map(transaction => (
                                    <TableRow key={transaction.id} onClick={() => handleCustomerSelect(customer)} hover>
                                        <TableCell>{customer.name}</TableCell>
                                        <TableCell>{transaction.id}</TableCell>
                                        <TableCell>{transaction.date}</TableCell>
                                        <TableCell>{transaction.amount}</TableCell>
                                    </TableRow>
                                ))
                        )}
                    </TableBody>
                </Table>
            </TableContainer>
            {selectedCustomer && (
                <Box my={4}>
                    <Typography variant="h5" gutterBottom>{selectedCustomer.name}'s Transactions</Typography>
                    <Line data={chartData} />
                </Box>
            )}
        </Container>
    );
};

export default App;
